"use client"


import { useState } from "react"
import api from '@/utils/api'


export default function Cadastro () {
   const [email, setEmail] = useState("")
   const [password, setPassword] = useState("")
   const [msg, setMsg] = useState("")

   const lidaComCadastro = async (e : React.FormEvent) => {
    e.preventDefault();

    try {
        await api.post("/cadastro", {email, password })
        setMsg("Usuario cadastrado")
    } catch {
        setMsg("Error no Cadastro do usuario")
    }
   }

   return (
    <div className="flex flex-col items-center justify-center h-screen">
        <form onSubmit={lidaComCadastro} className="flex flex-col gap-2">
             <input
              type="email"
              placeholder="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              className="border p-2 rounded"
              />
              <input 
              type="password"
              placeholder="password"
              value={password}
              onChange={(e) => setPassword(e.target.value)} 
              className="border p-2 rounded"
              
              />
              <button className="bg-green-600 text-white rounded p-2">Cadastrar</button>
        </form>
        <p>{msg}</p>
    </div>
   )
    
}

 